.about-container{
    width: 100%;
    display: flex;
    justify-content: center;
}
.about-section{
    padding-top: 55px;
    width: 950px;
    display: flex;
    flex-direction: column;
    text-align: start;
}
#about-title{
    width: 100%;
    padding-bottom: 60px;
    font-size: 54px;
}
.about-body{
    width: 100%;
    display: flex;
    justify-content: space-between;
}
.about-body div{
    width: 49%;
}
.about-body p{
    font-size: 21px;
    line-height: 32px;
    color: #38353F;
    align-items: center;
}
.background{
    height: 50px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
#bg-title{
    font-family: "Barlow", sans-serif;
    font-size: 18px;
    font-weight: 600;
    color: #706C75;
}
#bg-word{
    font-size: 16px;
    color: #706C75;
}
.about-body a{
    font-size: 21px;
    color: #38353F;
    font-weight: 500;
}
.paragraph p{
    margin-bottom: 20px;
}
.camera{
    font-size: 25px;
    margin-bottom: -5px;
}
.arrow{
    font-size: 19px;
}

.arrow-icon {
    font-size: 10px;
    margin-left: 5px;
}
.img{
    width: 100%;
    height: 350px;
    border-radius: 40px;
    margin: 50px 0 60px 0;
    overflow: hidden;
}
.img img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}
@media (max-width: 810px) {
    .about-section{
        width: 100%;
    }
    .about-body{
        flex-direction: column;
    }
    .about-body div{
        width: 100%;
    }
    .paragraph p{
        font-size: 18px;
        line-height: 23px;
    }
    .background{
        height: 75px;
        margin-bottom: 26px;
    }
    .bg-details{
        height: 50px;
    }
    #bg-word{
        line-height: 19px;
    }
    .img{
        height: 250px;
    }
}