* {
    font-family: "Barlow", sans-serif;
    padding: 0;
    margin: 0;
    color: rgb(56, 53, 63);
    box-sizing: border-box;
    transition: all 0.2s linear;
}

body {
    overflow-y: scroll;
}

p {
    font-family: "Roboto", sans-serif;
    font-weight: 400;
    color: #626069;
    font-size: 21px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-weight: 700;
}

a {
    text-decoration: none;
    font-size: 18px;
    font-weight: 700;
}

.container {
    width: 100%;
    display: flex;
    flex-direction: column;
}
.img-cover{
    width: 100%;
    height: 100%;
    background-color: #ffffff2d;
    position: relative;
    top: -101%;
    left: 0;
    z-index: 100;
}
nav {
    width: 100%;
    height: 5.5rem;
    padding-top: 30px;
    z-index: 1;
    background-color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    position: sticky;
    top: 0%;
    left: 25%;
}

.navbar {
    width: 950px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.link-btn {
    display: none;
}

.logo {
    height: 65%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}

.logo h1 {
    font-size: 16px;
}

.logo p {
    color: #aeadb3;
    font-size: 16px;
}

.links, .links1 {
    width: 35%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.each {
    width: 100%;
    height: 470px;
    background-color: #fff;
    background-image: linear-gradient(#fff, rgb(248, 250, 251));
    display: flex;
    justify-content: center;
}

header {
    padding-top: 45px;
    width: 950px;
    height: 100%;
    display: flex;
    justify-content: space-between;
}

.header-title {
    width: 48%;
    height: 100%;
    display: flex;
    flex-direction: column;
}

.header-title h1 {
    width: 100%;
    font-size: 54px;
    line-height: 95%;
}

header p {
    font-size: 21px;
    line-height: 150%;
    margin-top: 5px;
}

.header-img {
    width: 308px;
    height: 490px;
    position: relative;
    top: -5%;
    border-radius: 40px;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-left: 40px;
}

.header-img img {
    width: 150%;
    height: 170%;
    object-fit: cover;
}

.btn-a {
    width: 140px;
    margin-top: 36px;
    font-weight: 600;
}

.btn-a a:hover {
    text-decoration: underline;
    color: #626069;
}

section h2 {
    font-size: 34px;
    width: 460px;
}

section {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-top: 150px;
    text-align: center;
}

section .container {
    width: 950px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.noscroll-1 {
    width: 48%;
    height: 100%;
    gap: 35px;
    padding-left: 40px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.scroll-1 {
    width: 48%;
    gap: 35px;
    padding-top: 120px;
    padding-right: 40px;
    transition: 0.2s ease-out;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.service-cards {
    width: 100%;
    margin-top: 60px;
    display: flex;
    flex-wrap: wrap;
}

.service-card {
    width: 100%;
    height: 230px;
    border-radius: 20px;
    padding: 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-align: start;
}

.service-card p {
    font-size: 16px;
}

.service-card span {
    padding: 2px;
    border-radius: 8px;
}

.service-card-img {
    width: 30%;
    height: 90%;
    display: flex;
    align-items: center;
}

.service-card-img img {
    width: 100%;
    height: 100%;
    object-fit: contain;
}

.service-card .service-card-body {
    width: 67%;
    height: 90%;
    line-height: 200%;
}

.projects-container {
    width: 100%;
    margin-top: 80px;
    display: flex;
    justify-content: space-between;
}

.project-link {
    width: 100%;
}

.projects-container .card {
    width: 100%;
    border-radius: 40px;
    overflow: hidden;
    height: min-content;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    text-align: start;
    background-color: rgb(248, 250, 251);
}

.card .card-body {
    padding: 30px;
}

.card img {
    width: 100%;
    height: 255px;
    object-fit: cover;
}

.card h3 {
    font-size: 34px;
    line-height: 41px;
}

.card p {
    line-height: 32px;
}

.card:hover {
    scale: 108%;
    background-color: rgb(210, 235, 252);
}

.card-btns {
    width: 48%;
    margin-top: 10px;
    margin-bottom: 10px;
    display: flex;
    justify-content: space-between;
}

.card-btns span {
    padding: 6px 12px;
    font-size: 16px;
    font-weight: 400;
    font-family: "Roboto", sans-serif;
    background-color: rgba(0, 0, 0, 0.052);
    color: rgb(56, 53, 63);
    display: flex;
    align-items: center;
}

.arrow-icon {
    font-size: 10px;
    margin-left: 5px;
}

section .frame {
    width: 950px;
    height: 500px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}

.frame img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.frame p {
    margin-top: -30px;
}

.skills-image {
    width: 100%;
    height: 53%;
    border-radius: 40px;
    overflow: hidden;
}

.contact {
    height: 35vh;
    display: flex;
    flex-direction: column;
}

.contact * {
    margin: 11px;
}

.contact-logo {
    font-size: 24px;
}

.copyright{
    position: static;
    bottom: 0;
    left: 0;
}

@media screen and (max-width: 810px) {
    nav{
        flex-direction: column;
    }

    .navbar {
        width: 100%;
        padding: 25px;
        position: fixed;
        top: 0;
        left: 0;
    }
    
    .navbar-res{
        height: 330px;
        background-color: #fff;
    }
    .links1{
        display: none;
    }

    .links{
        width: 100%;
        height: 230px;
        padding: 20px;
        flex-direction: column;
        position: fixed;
        top: 5.5rem;
        left: 0;
    }
    .link-btn {
        width: 30px;
        height: 30px;
        font-size: 17px;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .each {
        height: 570px;
    }

    header {
        width: 100%;
        height: 570px;
        padding: 25px;
        flex-direction: column-reverse;
        justify-content: initial;
    }

    header p{
        font-size: 18px;
        line-height: 24px;
        margin-top: 9px;
    }

    .header-title {
        width: 100%;
        height: 50%;
        padding-top: 10px;
    }

    .header-title h1 {
        width: 70%;
        line-height: 40px;
        font-size: 36px;
    }
    .btn-a a{
       font-size: 16px;
    }

    .card h3 {
        font-size: 26px;
    }

    p {
        font-size: 18px;
    }

    .header-img {
        width: 196px;
        height: 224px;
    }

    section {
        padding-top: 30px;
        padding: 25px;
    }

    section .container {
        width: 100%;
    }

    section h2 {
        width: 356px;
        font-size: 26px;
    }

    .projects-body {
        width: 100%;
    }

    .projects-container {
        margin-top: 40px;
        flex-direction: column;
    }

    .noscroll-1 {
        width: 100%;
        padding-left: 0;
    }

    .scroll-1 {
        width: 100%;
        padding-right: 0;
        padding-top: 25px;
    }

    .card .card-body {
        padding: 24px 24px 0 24px;
    }

    section .frame {
        width: 100%;
    }

    #skills-section {
        height: 80vh;
        justify-content: center;
    }

    #contact-section {
        height: 45vh;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .contact {
        justify-content: center;
    }

    #projects-section {
        padding-top: 50px;
    }
}